import { PSPDFKitError } from '@pspdfkit/shared'
import { invariant } from '@pspdfkit/shared'
import { Base64 } from 'js-base64'

/**
 * This function can be used to validate a JWT. Will throw a PSPDFKitError if
 * the token is invalid.
 * Can also be used to log deprecation warnings.
 */
export function validateJWT(jwt: string): void {
  const JWT_INVALID = `The supplied JWT is invalid. Please refer to our guides on how to set up authentication:
  https://pspdfkit.com/guides/web/current/server-backed/client-authentication/`
  const DOCUMENT_ID_NOT_STRING = `The supplied JWT is invalid. The field 'document_id' has to be a string value.
  Please refer to our guides for further information: https://pspdfkit.com/guides/web/current/server-backed/client-authentication/`

  // Our dashboard uses internal API to authenticate the request. In this case,
  // the `jwt` is not a valid JWT but another JSON.
  // We will bypass the validation in this case.
  if (jwt.indexOf('{"internal":') !== -1) {
    return
  }

  // We require a JWT to be a string with two dots
  invariant(typeof jwt === 'string' && jwt.split('.').length === 3, JWT_INVALID)

  // We parse the user data section of the JWT.
  let userContent

  try {
    const json = Base64.decode(jwt.split('.')[1])

    userContent = JSON.parse(json)
  } catch (e) {
    throw new PSPDFKitError(JWT_INVALID)
  }

  invariant(typeof userContent.document_id === 'string', DOCUMENT_ID_NOT_STRING)
}

export function validateDocumentAccessToken(accessToken: string): void {
  // noinspection SuspiciousTypeOfGuard
  if (typeof accessToken !== 'string') {
    throw new PSPDFKitError('`accessToken` must be of type string.')
  }
}
