/**
This type can be used to control rendering in relation to print.

When printing, annotation's `noView` and `noPrint` flags have to be honoured
according to the PDF spec:

- An annotation with the noView flag will not be rendered in the UI but may be printable.
- An annotation with the noPrint flag will not be printed.

From: https://pspdfkit.com/guides/web/current/annotations/annotation-flags/
 */
type RenderForPrinting = boolean

export default class ServerBackend extends SharedBackend {
  provider: RESTProvider | InstantProvider
  _state: ServerBackendState
  _requestQueue: RequestQueue
  _password: string | null | undefined = null
  _refreshSignaturesInfoPromise: Promise<void> | null | undefined
  type: BackendType = 'SERVER'

  constructor(configuration: ServerConfiguration, _window: Window = window) {
    super()

    // Validate authPayload is an object
    if (!(typeof configuration.authPayload === 'object')) {
      throw new PSPDFKitError(
        "authPayload must be an object that contains the `jwt`. For example: `authPayload: { jwt: 'xxx.xxx.xxx'}`",
      )
    }

    const accessToken = configuration.authPayload?.accessToken

    let serverUrl
    let documentUrl

    if (!accessToken) {
      serverUrl = resolveServerUrl(configuration, _window)

      // Validate document ID
      if (typeof configuration.documentId !== 'string') {
        throw new PSPDFKitError('`documentId` must be of type string.')
      }

      documentUrl = `${serverUrl}i/d/${configuration.documentId}`

      // Validate authPayload
      // noinspection SuspiciousTypeOfGuard
      if (
        !(
          typeof configuration.authPayload === 'object' &&
          'jwt' in configuration.authPayload &&
          typeof configuration.authPayload.jwt === 'string'
        )
      ) {
        throw new PSPDFKitError(
          "authPayload must be an object that contains the `jwt`. For example: `authPayload: { jwt: 'xxx.xxx.xxx'}`",
        )
      }

      validateJWT(configuration.authPayload.jwt)
    } else {
      serverUrl = null
      documentUrl = null

      validateDocumentAccessToken(accessToken)

      if (typeof configuration.hostedBaseUrl !== 'string') {
        throw new PSPDFKitError('`hostedBaseUrl` must be of type string.')
      }
    }

    validateInstantSetting(configuration.instant)

    let instantSettings: InstantSettings | null | undefined = null

    if (configuration.instant) {
      if (isPlainObject(configuration.instant)) {
        const settings = configuration.instant

        instantSettings = {
          clientsPresenceEnabled: settings.clientsPresenceEnabled !== false,
          listenToServerChangesEnabled: settings.listenToServerChangesEnabled !== false,
        }
      } else {
        instantSettings = defaultInstantSettings
      }
    }

    this._requestQueue = new RequestQueue(MAX_RESOURCE_REQUESTS_IN_QUEUE_SERVER)

    const forceLegacySignaturesFeature = configuration.electronicSignatures
      ? Boolean(configuration.electronicSignatures.forceLegacySignaturesFeature)
      : false

    this._state = new ServerBackendState({
      serverUrl,
      hostedBaseUrl: configuration.hostedBaseUrl,
      documentId: configuration.documentId,
      instantSettings: instantSettings,
      documentURL: documentUrl,
      authPayload: configuration.authPayload,
      isFormsEnabled: !configuration.disableForms,
      forceLegacySignaturesFeature,
    })

    if (configuration['trustedCAsCallback']) {
      // TODO: Add link to guide article on Server
      warn(
        'PSPDFKit.Configuration#trustedCAsCallback is only used on Standalone deployments. On a Server-backed deployment, please follow the instructions at https://pspdfkit.com/guides/web',
      )
    }
  }

  isUsingInstantProvider(): boolean {
    return this._state.instantSettings != null
  }

  hasClientsPresence() {
    const instantSettings = this._state.instantSettings

    return instantSettings != null && instantSettings.clientsPresenceEnabled !== false
  }

  async load(options: LoadOptions = {}) {
    await this.tryAuthenticateHostedViewer()

    const {
      imageToken,
      token,
      permissions,
      features,
      signatureFeatureAvailability,
      hasPassword,
      minSearchQueryLength,
      layerHandle,
      allowedTileScales,
      digitalSignatures,
      defaultGroup,
      collaborationPermissions,
      creatorName,
    } = await authenticate(
      `${this._state.serverUrl}i/d/${this._state.documentId}`,
      this._state.authPayload as { jwt: string },
      options.password,
    )

    // Keep for reloading after applying operations
    this._password = options.password

    // check for Instant feature enabled in license if configured for Instant
    if (this._state.instantSettings && !features.includes(LicenseFeatures.INSTANT)) {
      throw new PSPDFKitError(`Instant feature is not enabled on this server. Please set \`instant\` to \`false\`.

For more information about PSPDFKit Instant please visit:
https://pspdfkit.com/guides/web/current/instant/overview/`)
    }

    const finalSignatureFeatureAvailability =
      signatureFeatureAvailability === SignatureFeatureAvailability.ELECTRONIC_SIGNATURES &&
      features.includes(LicenseFeatures.WEB_ANNOTATION_EDITING) &&
      this._state.forceLegacySignaturesFeature
        ? SignatureFeatureAvailability.LEGACY_SIGNATURES
        : signatureFeatureAvailability

    this._state = this._state.withMutations(state => {
      return state
        .set('imageToken', imageToken)
        .set('token', token)
        .set('features', List(features))
        .set('signatureFeatureAvailability', finalSignatureFeatureAvailability)
        .set(
          'backendPermissions',
          new BackendPermissions({
            readOnly: permissions.indexOf('write') === -1,
            downloadingAllowed: permissions.indexOf('download') >= 0,
          }),
        )
        .set(
          'documentURL',
          `${this._state.serverUrl}i/d/${this._state.documentId}/h/${layerHandle}`,
        )
        .set('documentHandle', layerHandle)
        .set('isDocumentHandleOutdated', false)
        .set('digitalSignatures', deserializeSignaturesInfo(digitalSignatures))
    })

    if (!!collaborationPermissions && !this._state.instantSettings) {
      throw new PSPDFKitError(
        'Collaboration Permissions is not supported when `instant` is disabled. Please make sure `configuration#instant` is set to `true`.',
      )
    }

    this._state = this._state.withMutations(state => {
      state.defaultGroup = defaultGroup
      state.hasCollaborationPermissions = Boolean(collaborationPermissions)
    })

    if (this.provider) {
      this.provider.destroy()
    }

    this.provider = await this._initProvider()
    this._state.instantSettings &&
      this.provider.setDocumentHandleConflictCallback(this.handleDocumentHandleConflict)

    return {
      features: this._state.features,
      signatureFeatureAvailability: this._state.signatureFeatureAvailability,
      hasPassword,
      minSearchQueryLength,
      allowedTileScales,
      jsActionChanges: [],
      creatorName,
      defaultGroup,
    }
  }

  private async tryAuthenticateHostedViewer() {
    if ('accessToken' in this._state.authPayload) {
      const { hostedBaseUrl } = this._state
      const accessToken = this._state.authPayload.accessToken

      const { serverUrl, serverId, jwt } = await authenticateHostedDocument(
        hostedBaseUrl,
        accessToken,
      )

      this._state = this._state.withMutations(state => {
        state
          .set('serverUrl', serverUrl)
          .set('documentId', serverId)
          .set('documentURL', `${serverUrl}i/d/${serverId}`)
          .set('authPayload', { jwt })
      })
    }
  }

  async _initProvider(): Promise<RESTProvider | InstantProvider> {
    if (this._state.instantSettings) {
      const documentURL = `${this._state.serverUrl}i/d/${this._state.documentId}/h/${this._state.documentHandle}`
      const { InstantProvider } = await import(
        /* webpackChunkName: "server-instant" */
        '../InstantProvider/InstantProvider'
      )

      return new InstantProvider(
        `${this._state.serverUrl}i/d/${this._state.documentId}`,
        documentURL,
        {
          auth_token: this._state.token,
        },
        this._state.instantSettings,
      )
    } else {
      const isFormsEnabled =
        this._state.isFormsEnabled && this._state.features.includes(LicenseFeatures.FORMS)

      const { RESTProvider } = await import(
        /* webpackChunkName: "server-rest" */
        '../RESTProvider/RESTProvider'
      )

      return new RESTProvider(
        this._state.documentURL,
        {
          token: this._state.token,
        },
        {
          isFormsEnabled,
        },
      )
    }
  }

  destroy(): void {
    this._destroyProvider()

    this._requestQueue && this._requestQueue.destroy()
  }

  documentInfo(): Promise<DocumentResponse> {
    return this._fetch(`${this._state.documentURL}/document.json`)
      .then(res => res.json())
      .then(payload => payload.data)
  }

  getFormJSON(): Promise<FormResponse> {
    return this._fetch(`${this._state.documentURL}/form.json`).then(res => {
      if (res.status === 403) {
        return {
          v: 1,
          type: 'pspdfkit/form',
          annotations: [],
          fields: [],
        }
      } else {
        return res.json().then(json => {
          const formResponse: FormResponse = json.data

          return formResponse
        })
      }
    })
  }

  async evalFormValuesActions() {
    throw new Error('not implemented')
  }

  async evalScript() {
    throw new Error('not implemented')
  }

  permissions(): Promise<BackendPermissions> {
    return Promise.resolve(this._state.backendPermissions)
  }

  getDefaultGroup(): string | null | undefined {
    return this._state.defaultGroup
  }

  isCollaborationPermissionsEnabled() {
    return this._state.hasCollaborationPermissions
  }

  textForPageIndex(pageIndex: number): {
    promise: Promise<TextResponse>
    cancel: IFunction
  } {
    const pageTextRequestURL = `${this._state.documentURL}/page-${pageIndex}-text`
    const jsonRequestPromise = new (this._getJSONRequestHandler())(
      pageTextRequestURL,
      this._state.token,
    )

    const request = this._requestQueue.enqueue(jsonRequestPromise, true)

    const jsonRequestQueuePromise = request.promise.then(json => jsonToTextLines(json, pageIndex))

    return {
      promise: jsonRequestQueuePromise,
      cancel: () => {
        request.cancel()
      },
    }
  }

  getTextFromRects(pageIndex: number, rects: List<Rect>): Promise<string> {
    const rectsParam = encodeURIComponent(JSON.stringify(rects.map(rectToJSON).toArray()))

    return this._fetch(
      `${this._state.documentURL}/page-${pageIndex}-highlighted?rects=${rectsParam}`,
    )
      .then(res => res.json())
      .then(response => response.text)
  }

  _getJSONRequestHandler() {
    return JSONRequest
  }

  renderTile(
    pageIndex: number,
    pageSize: Size,
    tileRect: Rect,
    renderForPrinting: RenderForPrinting,
    contentState?: {
      annotations: List<Annotation>
      formFieldValues: List<FormFieldValue>
      attachments: Map<string, Attachment>
      formFields: List<FormField>
      signatures?: SignatureInfo[]
    } | null,
  ): {
    promise: Promise<ImageHandle>
    cancel: IFunction
  } {
    if (this._state.isDocumentHandleOutdated) {
      return {
        promise: new Promise(() => {}),
        cancel: () => {},
      }
    }

    const imageTileURL = `${this._state.documentURL}/page-${pageIndex}-dimensions-${
      pageSize.width
    }-${pageSize.height}-tile-${tileRect.left}-${tileRect.top}-${tileRect.width}-${
      tileRect.height
    }${renderForPrinting ? '-print' : ''}`
    const hasPriority = pageSize.width === tileRect.width && pageSize.height === tileRect.height
    const doNotRequestWebP =
      tileRect.width > MAX_WEBP_DIMENSION || tileRect.height > MAX_WEBP_DIMENSION
    let resolvePromise,
      rejectPromise,
      isCanceled = false,
      hasRetried = false,
      // eslint-disable-next-line prefer-const
      requestPromises: any = [],
      // eslint-disable-next-line prefer-const
      requestImages: any[] = []

    const promise = new Promise<ImageHandle>((resolve, reject) => {
      resolvePromise = resolve
      rejectPromise = reject
    })

    const requestTile = (includeAttachmentIds: Array<string> = []) => {
      let imageRequestPromise

      if (contentState) {
        const formData = new FormData()

        formData.append(
          'data',
          JSON.stringify({
            annotations: contentState.annotations
              .filter(isModifiableAnnotation)
              .map(serializeAnnotation)
              .toJS()
              .map(content => ({
                content,
              })),
            formFieldValues: contentState.formFieldValues.map(serializeFormFieldValue).toJS(),
            formFields: contentState.formFields.map(serializeFormField).toJS(),
            signatures: contentState.signatures || [],
          }),
        )

        if (includeAttachmentIds.length > 0) {
          includeAttachmentIds.forEach(id => {
            const attachment = contentState.attachments.get(id)

            invariant(attachment && attachment.data, 'Attachment data could not be found.')
            formData.append(id, attachment.data)
          })
        }

        imageRequestPromise = new XHRImageRequest({
          identifier: imageTileURL,
          url: imageTileURL,
          token: this._state.imageToken,
          payload: formData,
          doNotRequestWebP,
        })
      } else {
        imageRequestPromise = new XHRImageRequest({
          identifier: imageTileURL,
          url: imageTileURL,
          token: this._state.imageToken,
          doNotRequestWebP,
        })
      }

      requestImages.push(imageRequestPromise)

      const imageRequest = this._requestQueue.enqueue(imageRequestPromise, hasPriority)

      imageRequest.promise
        .then(tile => {
          if (isCanceled) {
            return
          }

          if (tile.attachmentsNotFound && !hasRetried) {
            hasRetried = true
            requestTile(tile.attachmentsNotFound)

            return
          }

          if (tile.attachmentsNotFound) {
            rejectPromise(new PSPDFKitError('Attachment could not be found.'))

            return
          }

          resolvePromise(tile)
        })
        .catch(error => {
          if (isCanceled) {
            return
          }

          rejectPromise(error)
        })
      requestPromises.push(imageRequest)
    }

    requestTile()

    return {
      promise,
      cancel: () => {
        isCanceled = true
        requestImages.forEach(request => {
          if (request.abort && typeof request.abort === 'function') request.abort()
        })
        requestPromises.forEach(request => {
          request.cancel()
        })
      },
    }
  }

  /**
   * Common method for renderAnnotation and renderDetachedAnnotation.
   */
  _requestRenderAnnotation = (
    annotation: Annotation,
    formFieldValue: FormFieldValue | null | undefined,
    attachment: Attachment | null | undefined,
    width: number,
    height: number,
    detached?: boolean,
  ) => {
    const renderAnnotationURL = `${this._state.documentURL}/render_annotation`
    const requestQueueId = `render-annotation-${detached ? ulid() : annotation.id}`
    const annotationJSON = JSON.stringify({
      data: serializeAnnotation(annotation),
      width: width,
      height: height,
      detached: detached || undefined,
      formFieldValue: formFieldValue ? serializeFormFieldValue(formFieldValue) : undefined,
    })
    let resolvePromise,
      rejectPromise,
      isCanceled = false,
      // eslint-disable-next-line prefer-const
      hasRetried = false,
      // eslint-disable-next-line prefer-const
      requestPromises: any[] = []
    const promise = new Promise((resolve, reject) => {
      resolvePromise = resolve
      rejectPromise = reject
    })

    const requestAnnotation = (includeAttachmentIds: Array<string> = []) => {
      const formData = new FormData()

      formData.append('render', annotationJSON)

      if (
        includeAttachmentIds.length > 0 &&
        annotation.imageAttachmentId &&
        attachment &&
        attachment.data
      ) {
        formData.append(annotation.imageAttachmentId, attachment.data)
      }

      const doNotRequestWebP = width > MAX_WEBP_DIMENSION || height > MAX_WEBP_DIMENSION
      const imageRequestPromise = new XHRImageRequest({
        identifier: requestQueueId,
        url: renderAnnotationURL,
        token: this._state.imageToken,
        payload: formData,
        doNotRequestWebP,
      })

      const imageRequest = this._requestQueue.enqueue(imageRequestPromise, false)

      imageRequest.promise
        .then(result => {
          if (isCanceled) {
            return
          }

          if (result.attachmentsNotFound && !hasRetried) {
            requestAnnotation(result.attachmentsNotFound)

            return
          }

          if (result.attachmentsNotFound) {
            rejectPromise(new PSPDFKitError('Attachment could not be found.'))

            return
          }

          resolvePromise(result)
        })
        .catch(error => {
          if (isCanceled) {
            return
          }

          rejectPromise(error)
        })
      requestPromises.push(imageRequest)
    }

    requestAnnotation()

    return {
      promise,
      cancel: () => {
        isCanceled = true
        requestPromises.forEach(request => {
          request.cancel()
        })
      },
    }
  }

  _requestRenderAnnotations = (
    pageIndex: number,
    annotationsPdfObjectIds: Array<number>,
    widths: Array<number>,
    heights: Array<number>,
    formFieldValues: Array<FormFieldValueJSON>,
  ): DiscardablePromise<Array<Blob | null | undefined>> => {
    const renderAnnotationsURL = `${this._state.documentURL}/render_annotations`

    const renderRequestJSON = JSON.stringify({
      annotations: annotationsPdfObjectIds.map((pdfObjectId, index) => ({
        pageIndex,
        pdfObjectId,
        width: widths[index],
        height: heights[index],
      })),
      formFieldValues,
    })

    let resolvePromise
    let rejectPromise
    let isCanceled = false

    const promise = new Promise<Array<Blob | null | undefined>>((resolve, reject) => {
      resolvePromise = resolve
      rejectPromise = reject
    })

    this._fetch(renderAnnotationsURL, {
      method: 'post',
      body: renderRequestJSON,
      credentials: 'include',
      headers: {
        'X-PSPDFKit-Image-Token': this._state.imageToken,
        'Content-Type': 'application/json',
        Accept: 'multipart/form-data',
      },
    })

      .then(res => res.formData())
      .then((result: FormData) => {
        if (isCanceled) {
          return
        }

        // @ts-expect-error
        resolvePromise(Array.from(result.values()))
      })
      .catch(error => {
        if (isCanceled) {
          return
        }

        rejectPromise(error)
      })

    return {
      promise,
      cancel: () => {
        isCanceled = true
      },
    }
  }

  renderAnnotation(
    annotation: Annotation,
    formFieldValue: FormFieldValue | null | undefined,
    attachment: Attachment | null | undefined,
    width: number,
    height: number,
  ): {
    promise: Promise<ImageHandle | null | undefined>
    cancel: IFunction
  } {
    // @ts-expect-error
    return this._requestRenderAnnotation(annotation, formFieldValue, attachment, width, height)
  }

  renderPageAnnotations(
    pageIndex: number,
    unfilteredAnnotations: List<Annotation>,
    scaleFactor: number,
  ): Promise<void> {
    if (!supportsFormDataInResponse) {
      // If formData() in responses is not (or not correctly) supported, fall back to individual AP stream rendering.
      const pageAPStreamsPromise = Promise.resolve()

      this.pageAPStreamsPromises = this.pageAPStreamsPromises.set(pageIndex, pageAPStreamsPromise)

      return pageAPStreamsPromise
    }

    const provider = this.provider

    if (!this.provider._readStateCallbacks) {
      return Promise.reject(new PSPDFKitError('Not initialized'))
    }

    const formFieldValues: Array<FormFieldValueJSON> = []

    // Filter out stamps, image and signature widgets, which do not get cached.
    const annotations = unfilteredAnnotations.filter(annotation => {
      // @ts-expect-error
      const { formField } = provider._readStateCallbacks.getAnnotationWithFormField(annotation.id)

      const isCacheable = isCacheableAnnotationAPStream(annotation, formField)

      if (isCacheable && formField && typeof annotation.pdfObjectId === 'number') {
        formFieldValues.push(getFormFieldValueJSON(formField, annotation))
      }

      return isCacheable && typeof annotation.pdfObjectId === 'number'
    })

    if (annotations.size === 0 && formFieldValues.length === 0) {
      return Promise.resolve()
    }

    const pageAPStreamsPromise = new Promise<void>((resolve, reject) => {
      const { promise: renderPageAnnotationsCall, cancel } = this._requestRenderAnnotations(
        pageIndex,
        annotations.map(annotation => annotation.pdfObjectId).toArray() as number[],
        annotations
          .map(annotation => Math.floor(annotation.boundingBox.width * scaleFactor))
          .toArray(),
        annotations
          .map(annotation => Math.floor(annotation.boundingBox.height * scaleFactor))
          .toArray(),
        formFieldValues,
      )

      renderPageAnnotationsCall
        .then((pageAPStreamsBlobs: Array<Blob | null | undefined>) => {
          // An array of blob with PNG images is received.
          const imageHandlePromises: Array<Promise<ImageHandle> | null | undefined> =
            pageAPStreamsBlobs.map(blob => blob && imageHandleFromBlob(blob))

          imageHandlePromises.forEach(async (imageHandlePromise, index) => {
            const imageHandle = await imageHandlePromise
            const annotation: Annotation | undefined = annotations.get(index)

            if (annotation) {
              const annotationAPStreamPromiseResolve = this.annotationAPStreamPromises.get(
                annotation.id,
              )

              // If there's a registered resolve callback, resolve with the imageHandle
              if (annotationAPStreamPromiseResolve) {
                this.annotationAPStreamPromises = this.annotationAPStreamPromises.delete(
                  annotation.id,
                )
                annotationAPStreamPromiseResolve(imageHandle)
              }

              // Cache the imageHandle
              imageHandle && this.cacheAPStream(imageHandle, annotation)
            }
          })
          Promise.all(imageHandlePromises).then(() => resolve())
        })
        .catch(error => {
          cancel()
          reject(error)
        })
    })

    // Store the promise so when cachedRenderAnnotation() is called, it can be awaited to ensure AP streams are available for rendering.
    this.pageAPStreamsPromises = this.pageAPStreamsPromises.set(pageIndex, pageAPStreamsPromise)

    return pageAPStreamsPromise
  }

  renderDetachedAnnotation(
    annotation: Annotation,
    attachment: Attachment | null | undefined,
    width: number,
    height: number,
  ): {
    promise: Promise<ImageHandle | null | undefined>
    cancel: IFunction
  } {
    // @ts-expect-error
    return this._requestRenderAnnotation(annotation, null, attachment, width, height, true)
  }

  async getAttachment(attachmentId: string): Promise<Blob> {
    try {
      const res = await this._fetch(`${this._state.documentURL}/attachments/${attachmentId}`)

      switch (res.status) {
        case 404:
          throw new PSPDFKitError('Attachment not Found.')

        case 200:
          return await res.blob()

        default:
          throw new PSPDFKitError('Bad Request.')
      }
    } catch (error) {
      throw new PSPDFKitError(`Could not fetch attachment from PSPDFKit Server. ${error}`)
    }
  }

  async search(
    searchTerm: string,
    startPageIndex: number,
    limitPages: number,
    caseSensitive: boolean,
    searchInAnnotations = false,
    searchType: ISearchType = SearchType.TEXT,
  ): Promise<List<SearchResult>> {
    const params = `q=${
      searchType === SearchType.PRESET
        ? searchTerm.replace(/_/g, '-')
        : encodeURIComponent(searchTerm)
    }&start=${startPageIndex}&limit=${limitPages}&type=${searchType}&include_annotations=${searchInAnnotations.toString()}&case_sensitive=${caseSensitive.toString()}`
    const url = `${this._state.documentURL}/search?${params}`
    const response = await new JSONRequest(url, this._state.token).request()

    return parseJsonSearchResults(response.data)
  }

  async searchAndRedact(
    term: string | ISearchPattern,
    options: {
      searchType: ISearchType
      searchInAnnotations: boolean
      caseSensitive: boolean
      annotationPreset: RedactionAnnotationPreset
    },
  ): Promise<List<Annotation>> {
    const { searchType, annotationPreset, searchInAnnotations, caseSensitive } = options
    const { color, fillColor, outlineColor, ...rest } = annotationPreset
    const res = await this._fetch(`${this._state.documentURL}/redactions`, {
      method: 'post',
      credentials: 'include',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        strategy: searchType,
        strategyOptions: {
          [searchType as string]: searchType === SearchType.PRESET ? term.replace(/_/g, '-') : term,
          includeAnnotations: searchInAnnotations,
          caseSensitive: caseSensitive,
        },
        content: {
          ...rest,
          color: color && color.toHex(),
          fillColor: fillColor && fillColor.toHex(),
          outlineColor: outlineColor && outlineColor.toHex(),
        },
      }),
    })
    const { data } = await res.json()

    return List(
      data && data.annotations
        ? data.annotations.map(ann => RedactionAnnotationSerializer.fromJSON(ann.id, ann.content))
        : [],
    )
  }

  exportPDF({
    flatten = false,
    includeComments = true,
    excludeAnnotations = false,
  }: ExportPDFFlags = {}): Promise<ArrayBuffer> {
    const url = `${this._state.documentURL}/pdf?token=${this._state.token}&flatten=${String(
      flatten,
    )}&comments=${String(includeComments)}&render_ap_streams=${String(
      !flatten,
    )}&remove_annotations=${String(excludeAnnotations)}`

    return fetch(url, {
      method: 'GET',
      credentials: 'include',
    }).then(r => r.arrayBuffer())
  }

  exportXFDF(): Promise<string> {
    return this._fetch(`${this._state.documentURL}/document.xfdf`).then(res => res.text())
  }

  exportInstantJSON(): Promise<InstantJSON> {
    return this._fetch(`${this._state.documentURL}/instant.json`).then(res => res.json())
  }

  getPDFURL({ includeComments = true, excludeAnnotations = false }: GetPDFURLFlags = {}): {
    promise: Promise<string>
    revoke: IFunction
  } {
    return {
      promise: Promise.resolve(
        `${this._state.documentURL}/pdf?token=${this._state.token}&flatten=true&comments=${String(
          includeComments,
        )}&remove_annotations=${String(excludeAnnotations)}`,
      ),
      revoke: () => {},
    }
  }

  generatePDFObjectURL({
    includeComments = true,
    excludeAnnotations = false,
  }: GeneratePDFObjectURLFlags = {}): {
    promise: Promise<string>
    revoke: IFunction
  } {
    let objectUrl,
      isRevoked = false
    const promise = new Promise<string>(resolve => {
      this.exportPDF({
        flatten: true,
        includeComments,
        excludeAnnotations,
      }).then(arrayBuffer => {
        if (isRevoked) {
          return
        }

        const blob = new Blob([arrayBuffer], {
          type: 'application/pdf',
        })

        objectUrl = window.URL.createObjectURL(blob)
        resolve(objectUrl)
      })
    })

    return {
      promise,
      revoke: () => {
        if (objectUrl) {
          window.URL.revokeObjectURL(objectUrl)
        }

        isRevoked = true
      },
    }
  }

  async getDocumentOutline(): Promise<List<OutlineElement>> {
    let data

    try {
      const json = await this._fetch(`${this._state.documentURL}/outline.json`).then(res =>
        res.json(),
      )

      data = json.data
    } catch (e) {
      data = {}
    }

    const outline = Array.isArray(data.outline) ? data.outline : []

    return List(outline.map(documentOutlineFromJSON))
  }

  onKeystrokeEvent(): Promise<Array<ActionChange>> {
    throw new Error('not implemented')
  }

  async applyOperationsAndReload(operations: Array<DocumentOperationJSON>): Promise<LoadResponse> {
    try {
      const body = await createOperationsPayload(operations)

      this._destroyProvider()

      await this._fetch(`${this._state.documentURL}/apply-operations`, {
        method: 'post',
        body,
        credentials: 'include',
      })
    } catch (e) {
      throw new PSPDFKitError(`Applying operations failed: ${e}`)
    }

    return this.reloadDocument()
  }

  async applyRedactionsAndReload(): Promise<LoadResponse> {
    try {
      this._destroyProvider()

      await this._fetch(`${this._state.documentURL}/redact`, {
        method: 'post',
        credentials: 'include',
      })

      return this.reloadDocument()
    } catch (e) {
      // We rollback from destroying the provider if redacting fails. Otherwise,
      // no further updates are sent to the current provider.
      this.provider.load()
      throw new PSPDFKitError(`Applying redactions failed: ${e}`)
    }
  }

  async reloadDocument(): Promise<LoadResponse> {
    try {
      return await this.load({
        password: this._password,
      })
    } catch (e) {
      throw new PSPDFKitError(`Reloading the document failed: ${e}`)
    }
  }

  async exportPDFWithOperations(operations: Array<DocumentOperationJSON>): Promise<ArrayBuffer> {
    try {
      const body = await createOperationsPayload(operations)

      return this._fetch(`${this._state.documentURL}/pdf-with-operations`, {
        method: 'post',
        body,
        credentials: 'include',
      }).then(res => res.arrayBuffer())
    } catch (e) {
      throw new PSPDFKitError(`Exporting PDF with operations failed: ${e}`)
    }
  }

  async getSignaturesInfo(): Promise<SignaturesInfo> {
    if (this._refreshSignaturesInfoPromise) {
      await this._refreshSignaturesInfoPromise
    }

    return this._state.digitalSignatures
  }

  refreshSignaturesInfo(): Promise<void> {
    if (this._refreshSignaturesInfoPromise) {
      return this._refreshSignaturesInfoPromise
    }

    this._refreshSignaturesInfoPromise = new Promise((resolve, reject) => {
      this._fetch(`${this._state.documentURL}/signatures`, {
        method: 'get',
        credentials: 'include',
      })
        .then(res => res.json())
        .then(({ data: digitalSignatures }) => {
          this._state = this._state.set(
            'digitalSignatures',
            deserializeSignaturesInfo(digitalSignatures),
          )
          this._refreshSignaturesInfoPromise = null
          resolve()
        })
        .catch(error => {
          this._state = this._state.set('digitalSignatures', null)
          this._refreshSignaturesInfoPromise = null
          reject(error)
        })
    })

    return this._refreshSignaturesInfoPromise
  }

  async signDocumentAndReload(
    signaturePreparationData: SignaturePreparationData | null | undefined,
    signingServiceData: TwoStepSignatureCallback | (SigningServiceData | null | undefined),
  ): Promise<void> {
    try {
      if (signingServiceData !== undefined && typeof signingServiceData !== 'object') {
        throw new PSPDFKitError('Signing service data must be an object if specified.')
      }

      const additionalParams: any = {}

      if (signaturePreparationData && 'placeholderSize' in signaturePreparationData) {
        additionalParams.signerDataSource = {
          estimatedSize: signaturePreparationData.placeholderSize,
        }
      }

      if (signaturePreparationData && 'flatten' in signaturePreparationData) {
        additionalParams.flatten = signaturePreparationData.flatten
      }

      const body = {
        ...(signingServiceData
          ? {
              signingToken: signingServiceData.signingToken,
            }
          : null),
        ...additionalParams,
      }

      // We destroy the provider because during signing, a new form field and
      // widget annotation are created. Afterwards, we reload the document and
      // update the layerHandle, bailing out from the changes propagation back
      // to the managers flow.
      this._destroyProvider()

      await this._fetch(`${this._state.documentURL}/sign`, {
        method: 'post',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(body),
        credentials: 'include',
      })
      await this.reloadDocument()
    } catch (e) {
      // We rollback from destroying the provider if signing fails. Otherwise,
      // no further updates are sent to the current provider.
      this.provider.load()
      throw new PSPDFKitError(`Adding digital signature failed: ${e.message || e}`)
    }
  }

  handleDocumentHandleConflict = () => {
    this._state = this._state.set('isDocumentHandleOutdated', true)
    this.cancelRequests()

    this._destroyProvider()
  }

  getDocumentHandle() {
    return this._state.documentHandle
  }

  async getEmbeddedFiles(): Promise<List<EmbeddedFile>> {
    const res = await this._fetch(`${this._state.documentURL}/embedded-files`, {
      method: 'get',
      credentials: 'include',
    })

    const data = await res.json()

    return List(
      data?.data?.embeddedFiles?.length
        ? data.data.embeddedFiles.map(({ id, content }) => fromJSON(id, content))
        : [],
    )
  }

  // Cancels all current requests and removes all associated promises.
  cancelRequests() {
    this._requestQueue.cancelAll()
  }

  _destroyProvider() {
    if (this.provider) {
      // @ts-expect-error
      this.provider._clients && this.provider._clients.disconnect()
      this.provider.destroy()
    }
  }

  async _fetch(url: string, options?: RequestInit): Promise<Response> {
    const response = await fetch(url, {
      ...options,
      headers: {
        ...options?.headers,
        'X-PSPDFKit-Token': this._state.token,
        'PSPDFKit-Platform': 'web',
        'PSPDFKit-Version': getVersionHeader(),
      },
    })

    if (!response.ok) {
      let bodyMessage = await parseAsJSONOrString(response)

      bodyMessage = typeof bodyMessage === 'object' ? bodyMessage.reason : bodyMessage

      const message = bodyMessage || `${response.status} ${response.statusText}`

      throw new PSPDFKitError(message)
    }

    return response
  }

  syncChanges(): Promise<void> {
    return this.provider.syncChanges()
  }

  // No-op in Server mode.
  async clearAPStreamCache(): Promise<void> {}
}

function parseAsJSONOrString(response: Response) {
  // The response stream needs to be cloned before attempting
  // to read from it again (in case we need to fallback to text
  // parsing)
  return response
    .clone()
    .json()
    .catch(() => response.text())
}

async function createOperationsPayload(operations) {
  const operationsDocuments = {}
  // If multiple operations use the same blob, we only want to send it once.
  // We track blobs that have already been appended in a WeakMap(blob => index)
  const blobWeakMap = new WeakMap()
  const processedOperations = await Promise.all(
    operations.map(async (operation, index): Promise<DocumentOperationJSON> => {
      if (operation.type === 'importDocument') {
        const document = operation.document

        invariant(
          document instanceof File || document instanceof Blob,
          'Wrong `importDocument` operation `document` value: it must be a File or a Blob',
        )

        return addBlobToWeakMap(
          blobWeakMap,
          operationsDocuments,
          document,
          operation,
          index,
          'document',
        )
      } else if (operation.type === 'applyInstantJson') {
        const value = operation.instantJson

        invariant(
          typeof value === 'object' && value !== null,
          'Wrong `applyInstantJson` operation `instantJson` value: it must be an object',
        )

        const string = JSON.stringify(value)
        const document = new Blob([string], {
          type: 'application/json',
        })

        return addBlobToWeakMap(
          blobWeakMap,
          operationsDocuments,
          document,
          operation,
          index,
          'dataFilePath',
        )
      } else if (operation.type === 'applyXfdf') {
        const value = operation.xfdf

        invariant(
          typeof value === 'string',
          'Wrong `applyXfdf` operation `xfdf` value: it must be a string',
        )

        const document = new Blob([value], {
          type: 'application/vnd.adobe.xfdf',
        })

        return addBlobToWeakMap(
          blobWeakMap,
          operationsDocuments,
          document,
          operation,
          index,
          'dataFilePath',
        )
      }

      return operation
    }),
  )
  const formData = new FormData()

  formData.append(
    'operations',
    JSON.stringify({
      operations: processedOperations,
    }),
  )

  for (const document in operationsDocuments) {
    formData.append(document, operationsDocuments[document])
  }

  return formData
}

function validateInstantSetting(instant: boolean | InstantSettings) {
  let error = ''

  // Validate instant property.
  if (typeof instant !== 'boolean') {
    if (isPlainObject(instant)) {
      if (
        instant.hasOwnProperty('clientsPresenceEnabled') &&
        typeof instant['clientsPresenceEnabled'] !== 'boolean'
      ) {
        error +=
          '`clientsPresenceEnabled` in instance settings is not valid. Must be `true` or `false`.\n'
      }

      if (
        instant.hasOwnProperty('listenToServerChangesEnabled') &&
        typeof instant['listenToServerChangesEnabled'] !== 'boolean'
      ) {
        error +=
          '`listenToServerChangesEnabled` in instance settings is not valid. Must be `true` or `false`.\n'
      }
    } else {
      error = '`instant` flag must either be set to `true` or `false`\n'
    }

    if (error) {
      throw new PSPDFKitError(`${error}
For more information about PSPDFKit Instant please visit:
https://pspdfkit.com/guides/web/current/instant/overview/`)
    }
  }
}

function resolveServerUrl(configuration: ServerConfiguration, _window: Window = window): string {
  const serverUrl = configuration.serverUrl || getBaseUrl(_window.document)

  // serverUrl must have a slash at the end
  if (serverUrl.substr(-1) !== '/') {
    throw new PSPDFKitError(
      '`serverUrl` must have a slash at the end (e.g. `https://pspdfkit.example.com/`).',
    )
  }

  // Verify that the base URL is different from the current browser's location. If it's the same,
  // we assume that we haven't loaded PSPDFKit for Web from the Server origin and bail out. If
  // the `serverUrl` is explicitly set in the configuration, we will always use it
  if (!configuration.serverUrl) {
    // There were some problems with `window.location.origin` in IE11 on Win10 [1], that's why
    // we use `window.location.host` instead.
    // [1]: https://goo.gl/5D5mNX
    const browserOrigin = `${_window.location.protocol}//${_window.location.host}/`

    if (serverUrl === browserOrigin) {
      throw new PSPDFKitError(`PSPDFKit automatically infers the URL of PSPDFKit Server from the current \`<script>\` tag.
In the current case, this URL is set to the same as the current browser's location.
This can happen when you bundle pspdfkit.js with your custom JavaScript for example.

To make sure everything works as expected, please set the \`serverUrl\` to the URL of PSPDFKit Server:

PSPDFKit.load({
  serverUrl: "https://pspdfkit-server.example.com/",
  ...,
});`)
    }
  }

  return serverUrl
}
