import * as Immutable from 'immutable'
import { List } from 'immutable'
import BackendPermissions from '@/models/BackendPermissions'
import {
  ISignatureFeatureAvailability,
  SignatureFeatureAvailability,
} from '@/enums/SignatureFeatureAvailability'
import type { SignaturesInfo } from '@/models/digital-signatures/SignaturesInfo'
import type { InstantSettings } from '@/models/InstantSettings'

interface IServerBackendState {
  authPayload:
    | {
        jwt: string
      }
    | {
        accessToken: string
      }
    | null
  serverUrl: string | null
  hostedBaseUrl: string | null
  documentId: string | null
  backendPermissions: BackendPermissions | null
  documentURL: string | null
  imageToken: string | null
  token: string | null
  instantSettings: InstantSettings | null
  features: List<string>
  signatureFeatureAvailability: ISignatureFeatureAvailability
  isFormsEnabled: boolean
  documentHandle: string | null
  isDocumentHandleOutdated: boolean
  digitalSignatures: SignaturesInfo | null
  defaultGroup: string | null | undefined
  hasCollaborationPermissions: boolean
  forceLegacySignaturesFeature: boolean
  minSearchQueryLength: number
}

export default class ServerBackendState extends Immutable.Record<IServerBackendState>({
  authPayload: null,
  serverUrl: null,
  hostedBaseUrl: null,
  documentId: null,
  backendPermissions: null,
  documentURL: null,
  imageToken: null,
  instantSettings: null,
  token: null,
  features: List(),
  signatureFeatureAvailability: SignatureFeatureAvailability.NONE,
  isFormsEnabled: true,
  minSearchQueryLength: 1,
  documentHandle: null,
  isDocumentHandleOutdated: false,
  digitalSignatures: null,
  defaultGroup: undefined,
  hasCollaborationPermissions: false,
  forceLegacySignaturesFeature: false,
}) {
  /**
   * The auth payload used to create the PSPDFKit for Web instance. This is most likely an object
   * with a key `jwt` that contains the `jwt`.
   * We also use a special internal authentication token for the PSPDFKit Server dashboard, this
   * also creates an object here with the key `jwt` but the value is no valid JWT!
   * Hosted viewer uses a separate `accessToken` to authenticate with the hosted frontend.
   */
  override authPayload:
    | {
        jwt: string
      }
    | {
        accessToken: string
      }

  /**
   * URL to the PSPDFKit Server, e.g: `https://pspdfkit.example.com/`
   */
  override serverUrl: string

  /**
   * URL of the hosted frontend, e.g: `https://api.pspdfkit.com/
   */
  override hostedBaseUrl: string

  /**
   * The identifier of the document for PSPDFKit Server
   */
  override documentId: string

  /**
   * Defines if the user is allowed to edit annotations or download the document
   */
  override backendPermissions: BackendPermissions

  /**
   * URL to the document, e.g.: `https://pspdfkit.example.com/d/foobar`
   */
  override documentURL: string

  /**
   * This token is used to retrieve images from the Server.
   */
  override imageToken: string

  /**
   * The write token generated by PSPDFKit Server. This is used to authenticate all non image
   * loading requests.
   */
  override token: string

  /**
   * Holds settings for the instant provider. When present, InstantProvider is used to
   * communicate with the Server.
   */
  override instantSettings: InstantSettings

  /**
   * PSPDFKit license features, used to detect which features can be used.
   */
  override features: List<string>

  /**
   * Determines which variant of signatures UI is shown
   */
  override signatureFeatureAvailability: ISignatureFeatureAvailability

  /**
   * Scans the Configuration and the returned features to see if forms shall be implemented. This is
   * necessary since we initialize the RESTProvider before we can process this information in
   * other parts of the app.
   *
   * FIXME: Find a way to get the actual licenses when initializing RESTProvider so that we don't
   * have to maintain a separate implementation of form related feature tests.
   */
  override isFormsEnabled: boolean

  /**
   * ID string corresponding to the current document. This field is updated when operations
   * are applied on the document with the new document SHA256 hash.
   */
  override documentHandle: string

  /**
   * Set when the document has been modified by an external client, thereby modifying the document handle.
   * When set, the backend should authenticate again and load the modified document with the new `documentHandle`.
   */
  override isDocumentHandleOutdated: boolean

  /**
   * List of digital signatures present in the document.
   */
  override digitalSignatures: SignaturesInfo

  /**
   * Set the default group to use when records are created through the UI
   */
  override defaultGroup: string | null | undefined

  /**
   * Whether Collaboration Permissions are enabled or not
   */
  override hasCollaborationPermissions: boolean

  /**
   * Marks if even though Electronic Signatures licensed for the current user,
   * the legacy signature mode should be used instead.
   */
  override forceLegacySignaturesFeature: boolean
}
