/**
 * This object contains detailed Instant settings.
 *
 * @private
 * @memberof PSPDFKit
 * @interface InstantSettings
 * @server
 * @example
 * PSPDFKit.load({
 *   instant: {
 *      clientPresenceEnabled: false,
 *      listenToServerChangesEnabled: false
 *   }
 * });
 * @seealso PSPDFKit.Configuration.instant
 */

/**
 * Controls whether the client presence tracking is enabled.
 *
 * When this flag is set to `true`, a single WebSocket connection is kept open to get up to date
 * information about currently connected instance clients.
 *
 * When this flag is set to `false`, client presence tracking will be disabled. This means that
 * {@link PSPDFKit.Instance#connectedClients} will return an empty map and
 * the {@link PSPDFKit.Instance~ConnectedClientsChangeEvent} won't be triggered.
 *
 * @private
 * @instance
 * @member {boolean} clientsPresenceEnabled
 * @memberof PSPDFKit.InstantSettings
 * @default true
 */

/**
 * Controls whether to listen to server changes.
 *
 * When this flag is set to `true`, changes will be pulled immediately. Single HTTP connection is
 * kept open to listen to server changes in this case.
 *
 * When this flag is set to `false`, Instant won't listen to changes made by other connected clients.
 * It's clients responsibility to call {@link PSPDFKit.Instance#syncChanges} to pull latest changes.
 *
 * @private
 * @instance
 * @member {boolean} listenToServerChangesEnabled
 * @memberof PSPDFKit.InstantSettings
 * @default true
 */
export type InstantSettings = {
  clientsPresenceEnabled?: boolean
  listenToServerChangesEnabled?: boolean
}

export const defaultInstantSettings: InstantSettings = {
  clientsPresenceEnabled: true,
  listenToServerChangesEnabled: true,
}
