export type HostedDocumentAuthResponse = {
  jwt: string
  serverId: string
  serverUrl: string
}

/**
 * Calls the `/auth` endpoint of the Hosted API.
 */
export async function authenticateHostedDocument(
  hostedBaseUrl: string,
  accessToken: string,
): Promise<HostedDocumentAuthResponse> {
  const res = await fetch(`${hostedBaseUrl}/i/documents/auth`, {
    method: 'POST',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      'PSPDFKit-Platform': 'web',
      'PSPDFKit-Version': `cloud-protocol=${PSPDFKIT_DEFINES.HOSTED_CLOUD_PROTOCOL}, server-protocol=${PSPDFKIT_DEFINES.SERVER_PROTOCOL}, client=${PSPDFKIT_DEFINES.VERSION}, client-git=${PSPDFKIT_DEFINES.REVISION}`,
    },
    body: JSON.stringify({
      accessToken,
    }),
  })

  if (res.ok) {
    return res.json()
  } else {
    throw new Error(`An error occurred while connecting to PSPDFKit API: ${await res.text()}`)
  }
}
